:root
{
  --light-color: rgb(60, 58, 71);
  --color: rgb(37, 35, 44);
  --dark-color: rgb(18, 17, 22);
}

html, body, .app
{  
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
}

.app
{
  display: flex;
  flex-direction: column;
}

header
{
  background: var(--dark-color);
  display: flex;
  align-items: center;
  padding: 25px 0;
}

.header-button
{
  background: var(--light-color);
  color: white;  
  margin: 0 15px;
  padding: 15px 20px;
  font-size: 25px;
  border: none;
  outline: none;
  border-radius: 10px;
  transition: background 0.1s;
}

.header-button:hover
{
  background: rgb(90, 87, 109);
}

.header-spacer
{
  flex: 1;
}

.download-file-button p
{
  margin: 0;
}

.enter-filename
{
  background: var(--dark-color);
  display: flex;
  align-items: center;
  position: absolute;
  top: 120px;
  left: 100px;
  padding: 20px 25px;
  opacity: 0;
  z-index: 100;
  border-radius: 10px;
  pointer-events: none;
  transition: opacity 0.15 ease-out;
}

.enter-filename-button
{
  margin-right: 0;
  height: 50px;
  line-height: 20px;
}

.download-file-button:focus-within .enter-filename
{ 
  opacity: 1;
  pointer-events: auto;
}

.file-name
{
  color: white;
  margin-left: 20px;
  font-size: 25px;
  font-family: sans-serif;
}

input
{
  background: var(--light-color);
  color: white;
  width: 250px;
  height: 50px;
  margin-left: 15px;
  padding: 0 10px;
  font-size: 25px;
  border: none;
  outline: none;
  border-radius: 10px;
}

.main-section
{
  display: flex;
  height: 100%;
}

.editor-container
{
  display: flex;
  flex-direction: column;
  width: 100%;
}

.editor 
{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.CodeMirror
{
  flex-grow: 1;
  font-size: 25px;
  text-indent: 10px;
}

.output
{
  background: rgb(67, 65, 82);
  color: white;
  width: 750px;
  padding: 10px 15px 0 15px;
  font-size: 30px;
  font-family: sans-serif;
}